"use client";

import { FC } from "react";
import { VisuallyHidden } from "@react-aria/visually-hidden";
import { SwitchProps, useSwitch } from "@heroui/switch";
import { useTheme } from "next-themes";
import { useIsSSR } from "@react-aria/ssr";
import clsx from "clsx";

import { SunFilledIcon, MoonFilledIcon } from "@/components/icons";

export interface ThemeSwitchProps {
  className?: string;
  classNames?: SwitchProps["classNames"];
}

export const ThemeSwitch: FC<ThemeSwitchProps> = ({ className, classNames }) => {
  const { theme, setTheme } = useTheme();
  const isSSR = useIsSSR();

  const onChange = () => {
    setTheme(theme === "light" ? "dark" : "light");
  };

  const {
    Component,
    slots,
    isSelected,
    getBaseProps,
    getInputProps,
    getWrapperProps,
  } = useSwitch({
    isSelected: theme === "light" || isSSR,
    "aria-label": `Switch to ${theme === "light" || isSSR ? "dark" : "light"} mode`,
    onChange,
  });

  return (
    <Component
      {...getBaseProps({
        className: clsx(
          "px-1 py-1 cursor-pointer transition-all duration-500 ease-in-out",
          className,
          classNames?.base
        ),
      })}
    >
      <VisuallyHidden>
        <input {...getInputProps()} />
      </VisuallyHidden>

      <div
        {...getWrapperProps()}
        className={slots.wrapper({
          class: clsx(
            "relative w-14 h-8 flex items-center rounded-full p-1",
            "bg-gradient-to-r from-indigo-400 via-purple-500 to-pink-500",
            "hover:from-pink-500 hover:via-purple-500 hover:to-indigo-400",
            "transition-all duration-500 ease-in-out shadow-lg",
            classNames?.wrapper
          ),
        })}
      >
        {/* Sliding Circle */}
        <div
          className={clsx(
            "absolute top-0.5 left-0.5 w-7 h-7 rounded-full bg-white shadow-xl flex items-center justify-center",
            "transition-all duration-500 ease-in-out transform",
            isSelected ? "translate-x-6" : "translate-x-0"
          )}
        >
          {/* Icon */}
          {isSelected ? (
            <MoonFilledIcon
              size={20}
              className="text-purple-600 drop-shadow-md animate-pulse"
            />
          ) : (
            <SunFilledIcon
              size={20}
              className="text-yellow-400 drop-shadow-md animate-pulse"
            />
          )}
        </div>
      </div>
    </Component>
  );
};
