"use client";

import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";
import { AnimatePresence, motion } from "framer-motion";
import {
  Navbar as HeroUINavbar,
  NavbarContent,
  NavbarMenu,
  NavbarMenuToggle,
  NavbarBrand,
  NavbarItem,
  NavbarMenuItem,
} from "@heroui/navbar";
import { Kbd } from "@heroui/kbd";
import { Link } from "@heroui/link";
import { Input } from "@heroui/input";
import { Button } from "@heroui/button";
import { link as linkStyles } from "@heroui/theme";
import NextLink from "next/link";
import clsx from "clsx";

import { ThemeSwitch } from "@/components/theme-switch";
import { GithubIcon, SearchIcon, Logo } from "@/components/icons";

// ðŸ”¹ Animation variants for dropdown
const submenuVariants = {
  hidden: { opacity: 0, y: -10 },
  visible: { opacity: 1, y: 0 },
};

export const Navbar = () => {
  const router = useRouter();
  const [openMenu, setOpenMenu] = useState<string | null>(null);
  const menuRef = useRef<HTMLDivElement>(null);

  const toggleMenu = (menu: string) => {
    setOpenMenu(openMenu === menu ? null : menu);
  };

  // âœ… Close on outside click or Esc key
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setOpenMenu(null);
      }
    };

    const handleEsc = (event: KeyboardEvent) => {
      if (event.key === "Escape") {
        setOpenMenu(null);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    document.addEventListener("keydown", handleEsc);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.removeEventListener("keydown", handleEsc);
    };
  }, []);

  const searchInput = (
    <Input
      aria-label="Search"
      classNames={{
        inputWrapper: "bg-default-100",
        input: "text-sm",
      }}
      endContent={
        <Kbd className="hidden lg:inline-block" keys={["command"]}>
          K
        </Kbd>
      }
      labelPlacement="outside"
      placeholder="Search..."
      startContent={
        <SearchIcon className="text-base text-default-400 pointer-events-none flex-shrink-0" />
      }
      type="search"
    />
  );

  const retrieveInput = (
    <Input
      aria-label="Retrieve"
      classNames={{
        inputWrapper: "bg-default-100",
        input: "text-sm",
      }}
      placeholder="Retrieve Policy / ID..."
      type="text"
    />
  );

  return (
    <>
      {/* âœ… Topbar */}
      <div className="w-full bg-primary text-white text-sm px-6 py-2 flex items-center relative">
        {/* Centered links */}
        <div className="flex flex-wrap gap-4 justify-center w-full">
          {["Newsroom", "Careers", "Blog", "Complaints"].map((item) => (
            <Link
              key={item}
              href={`/${item.toLowerCase().replace(/\s+/g, "-")}`}
              className="hover:underline text-white"
            >
              {item}
            </Link>
          ))}
        </div>

        {/* Right side - Portals dropdown */}
        <div className="flex items-center gap-4 relative ml-auto" ref={menuRef}>
          {/* Portals Button */}
          <Button
            onPress={() => toggleMenu("portals")}
            className="bg-danger text-white font-semibold px-4 py-2 rounded-md transition-all duration-300"
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = "white";
              e.currentTarget.style.color = "#DC2626";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = "#DC2626";
              e.currentTarget.style.color = "white";
            }}
          >
            Portals
          </Button>

        {/* Dropdown menu */}
        <AnimatePresence>
          {openMenu === "portals" && (
            <motion.div
              initial="hidden"
              animate="visible"
              exit="hidden"
              variants={submenuVariants}
              className="absolute right-0 top-10 z-50 bg-white rounded-lg p-2 shadow-lg min-w-[220px]"
            >
              {[
                { name: "Agent Portal", path: "/agent-portal" },
                { name: "Broker Portal", path: "/broker-portal" },
                { name: "Provider Portal", path: "/provider-portal" },
                { name: "Intermediary Registration", path: "/intermediary-registration" },
                { name: "Agent Registration", path: "/agent-registration" },
                { name: "Group Registration", path: "/group-registration" },
              ].map((item) => (
                <div
                  key={item.name}
                  className="px-3 py-2 rounded-md cursor-pointer text-primary hover:text-danger hover:bg-gray-100 transition-colors"
                  onClick={() => {
                    router.push(item.path);
                    setOpenMenu(null);
                  }}
                >
                  {item.name}
                </div>
              ))}
            </motion.div>
          )}
        </AnimatePresence>
        </div>
      </div>

      {/* âœ… Navbar with danger bg */}
      <HeroUINavbar
        maxWidth="xl"
        position="sticky"
        className="bg-danger text-white"
      >
        {/* Left side (brand + nav items) */}
        <NavbarContent className="basis-1/5 md:basis-full" justify="start">
          <NavbarBrand as="li" className="gap-3 max-w-fit">
            <NextLink className="flex justify-start items-center gap-1" href="/">
              <Logo />
              <p className="font-bold text-inherit">Birdview</p>
            </NextLink>
          </NavbarBrand>
          <ul className="hidden lg:flex gap-4 justify-start ml-2">
            {[
              { label: "Home", href: "/" },
              { label: "About Us", href: "/about-us" },
              { label: "Our Leadership", href: "/our-leadership" },
              { label: "Our Management", href: "/team-members" },
              { label: "Careers", href: "/careers" },
              { label: "Contact Us", href: "/contact-us" },
            ].map((item) => (
              <NavbarItem key={item.href}>
                <NextLink
                  className={clsx(
                    linkStyles({ color: "foreground" }),
                    "data-[active=true]:text-primary data-[active=true]:font-medium"
                  )}
                  color="foreground"
                  href={item.href}
                >
                  {item.label}
                </NextLink>
              </NavbarItem>
            ))}
          </ul>
        </NavbarContent>

        {/* Right side (theme, search, retrieve) */}
        <NavbarContent
          className="hidden md:flex basis-1/5 md:basis-full"
          justify="end"
        >
          <NavbarItem className="hidden lg:flex">{searchInput}</NavbarItem>
          <NavbarItem className="hidden lg:flex">{retrieveInput}</NavbarItem>
          <NavbarItem className="hidden md:flex gap-2">
            <ThemeSwitch />
          </NavbarItem>
        </NavbarContent>

        {/* Mobile menu toggle */}
        <NavbarContent className="md:hidden basis-1 pl-4" justify="end">
          <Link isExternal aria-label="Github" href="https://github.com/">
            <GithubIcon className="text-white" />
          </Link>
          <ThemeSwitch />
          <NavbarMenuToggle />
        </NavbarContent>

        {/* Mobile menu */}
        <NavbarMenu>
          {searchInput}
          {retrieveInput}
          <div className="mx-4 mt-2 flex flex-col gap-2">
            {[
              { label: "Home", href: "/" },
              { label: "About Us", href: "/about-us" },
              { label: "Our Leadership", href: "/our-leadership" },
              { label: "Our Management", href: "/team-members" },
              { label: "Careers", href: "/careers" },
              { label: "Contact Us", href: "/contact-us" },
            ].map((item, index) => (
              <NavbarMenuItem key={`${item.href}-${index}`}>
                <Link
                  color={
                    index === 2
                      ? "primary"
                      : index === 5
                      ? "danger"
                      : "foreground"
                  }
                  href={item.href}
                  size="lg"
                >
                  {item.label}
                </Link>
              </NavbarMenuItem>
            ))}
          </div>
        </NavbarMenu>
      </HeroUINavbar>
    </>
  );
};
